/* -------------------------------------------------------------------------- 
	#. Variables
  -------------------------------------------------------------------------- */

:root {

    // Base colors.
    --adfy_wishlist_text_color: #111111;
    --adfy_wishlist_link_color: blue;
    --adfy_wishlist_link_hover_color: #111111;

    // Button colors.
    --adfy_wishlist_button_color: #333333;
    --adfy_wishlist_button_hover_color: #111111;
    --adfy_wishlist_button_text_color: #111111;
    --adfy_wishlist_button_text_hover_color: #111111;
    --adfy_wishlist_button_border_color: transparent;
    --adfy_wishlist_button_border_hover_color: transparent;
    --adfy_wishlist_button_border_radius: 0;
    --adfy_wishlist_button_border_width: 0;
    --adfy_wishlist_button_border_style: solid;

    // Modal box colors.
    --adfy_wishlist_popup_notice_text_color: #333333;
    --adfy_wishlist_popup_notice_background_color: #FFFFFF;
    --adfy_wishlist_notice_popup_icon_color: #858585;
    --adfy_wishlist_notice_popup_button_color: white;
    --adfy_wishlist_notice_popup_button_hover_color: white;
    --adfy_wishlist_notice_popup_button_background_color: rgba(150, 88, 138, 1);
    --adfy_wishlist_notice_popup_button_background_hover_color: rgb(154, 60, 135);


    /**
    * #. Development
    */

    // Button colors.

    --adfy_wishlist_wishlist_btn_text_color: white;
    --adfy_wishlist_wishlist_btn_icon_color: white;
    --adfy_wishlist_wishlist_btn_text_color_hover: white;
    --adfy_wishlist_wishlist_btn_icon_color_hover: white;
    --adfy_wishlist_wishlist_btn_bg_color: #444444;
    --adfy_wishlist_wishlist_btn_bg_color_hover: #111111;

    // Popup Modal Colors.

    --adfy_wishlist_popup_modal_bg_color: white; // Modal background
    --adfy_wishlist_popup_modal_overlay_bg_color: rgba(0, 0, 0, 0.8); // Modal overlay background
    --adfy_wishlist_popup_modal_icon_color: #7E7E7E;
    --adfy_wishlist_popup_modal_btn_text_color: #444444;
    --adfy_wishlist_popup_modal_btn_text_color_hover: #444444;
    --adfy_wishlist_popup_modal_btn_bg_color: white;

    // Sidebar Colors.

    --adfy_wishlist_sidebar_modal_toggle_btn_label_color: #111111;
    --adfy_wishlist_sidebar_modal_toggle_btn_label_color_hover: #111111;
    --adfy_wishlist_sidebar_modal_toggle_btn_bg_color: #333333;
    --adfy_wishlist_sidebar_modal_bg_color: white;
    --adfy_wishlist_sidebar_modal_title_color: #111111;
    --adfy_wishlist_sidebar_modal_empty_text_color: #111111;
    --adfy_wishlist_sidebar_modal_close_icon_color: #111111;
    --adfy_wishlist_sidebar_modal_close_icon_color_hover: #111111;
    --adfy_wishlist_sidebar_modal_product_title_color: #111111;
    --adfy_wishlist_sidebar_modal_product_regular_price_color: #111111;
    --adfy_wishlist_sidebar_modal_product_sale_price_color: #111111;
    --adfy_wishlist_sidebar_modal_product_add_to_cart_label_color: #111111;
    --adfy_wishlist_sidebar_modal_product_add_to_cart_label_color_hover: #111111;
    --adfy_wishlist_sidebar_modal_product_add_to_cart_bg_color: #333333;
    --adfy_wishlist_sidebar_modal_product_add_to_cart_bg_color_hover: #111111;
    --adfy_wishlist_sidebar_modal_product_remove_from_wishlist_icon_color: #111111;
    --adfy_wishlist_sidebar_modal_view_wishlist_btn_label_color: #111111;
    --adfy_wishlist_sidebar_modal_view_wishlist_btn_label_color_hover: #111111;
    --adfy_wishlist_sidebar_modal_view_wishlist_btn_bg_color: #333333;
    --adfy_wishlist_sidebar_modal_view_wishlist_btn_bg_color_hover: #111111;
    --adfy_wishlist_sidebar_modal_notification_text_color: #111111;
    --adfy_wishlist_sidebar_modal_notification_bg_color: #333333;



    // Zindex
    --adfy_wishlist_popup_modal_zindex: 999999999999;
}